* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "fontset", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "fontset";
  src: url(./GenSekiGothic-B.ttc);
}
header,
body {
  height: 100%;
}

header {
  background: linear-gradient(to right, #ffffff, #6dd5fa, #2980b9);
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
header div.logo {
  display: flex;
  align-items: center;
  flex: 3 1 400px;
  padding: 0.75rem 1rem;
}
header div.logo img.plg {
  max-width: 12%;
  height: auto;
  vertical-align: middle;
}
header div.logo img.t1 {
  max-width: 18%;
  height: auto;
  vertical-align: middle;
}

nav {
  flex: 2 1 400px;
}
nav ul {
  display: flex;
  list-style-type: none;
  justify-content: space-around;
}
nav ul li a {
  padding-right: 2rem;
  color: hsla(225, 49%, 18%, 0.5);
  text-decoration: none;
  font-size: 1.5rem;
}

.active {
  color: white;
}

nav ul li a:hover {
  color: rgba(255, 255, 255, 0.8);
  transition: 1s;
}/*# sourceMappingURL=Navbar.css.map */